<template>
    <section>
        <div class="my-popups">
            <loader :is-visible="isLoading"></loader>
            <div class="modal d-block">
                <div class="modal-dialog modal-dialog-centered">
                    <div class="modal-content">
                        <div class="modal-header theme-primary partition-full d-flex">
                            <h5 class="modal-title">{{ $t('ManageBatch') }}</h5>
                            <button type="button" class="close" v-on:click="Close">
                                <em class="fa fa-times" aria-hidden="true"></em>
                            </button>
                        </div>

                        <div class="modal-body">
                            <dynamic-form lang="en" ref="validateBatch" :buttons="buttons" :schema="FormSchema">
                                <template v-slot:tgslot-Occurs_Every="{ data }">
                                    <Field name="Occurs Every" :rules="{ 'required': true }" v-slot="{ errors }">
                                        <div class="input-group">
                                            <input type="number"
                                                v-on:keyup="checkQuantity($event, data.fieldInfo, data.fieldInfo.value)"
                                                id="" v-model="data.fieldInfo.value" min="1" maxlength="8"
                                                v-bind:class="{ 'txtitems form-control ': true, }" />
                                            <span class="input-group-append">
                                                <span class="input-group-text" id="spnOutOF">Hours</span>
                                            </span>
                                        </div>
                                        <span v-if="errors[0]" name="Occurs Every" class="invalid-feedback d-block">{{
                                            errors[0]
                                            }}</span>
                                    </Field>
                                </template>
                        <template v-slot:tgslot-BillingFrequencyType="{ data }">
                        <div class="row">
                            <div v-if="BillFrequencyVal === 'bymonthly'"
                                class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left mb-3 ">
                                <select v-model="form.byMonthly" class="form-control large-select" :v-model="form.byMonthly">
                                    <option value="">{{ $t('Select') }}</option>
                                    <option v-for="month in months" :key="month" :value="month">{{ month }}</option>
                                </select>
                                <small class="text-muted">{{ $t('CycleRange') }}: {{ getCycleRange(2,form.byMonthly) }}</small>
                            </div>
                            <!-- Weekly -->
                            <div v-if="BillFrequencyVal === 'week'"
                                class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left mb-3">
                                <select v-model="form.weeklyDay" class="form-control large-select" :v-model="form.weeklyDay">
                                    <option value="">{{ $t('Select') }}</option>
                                    <option v-for="day in daysOfWeek" :key="day" :value="day">{{ day }}</option>
                                </select>
                            </div>

                            <!-- Bi-weekly -->
                            <div v-if="BillFrequencyVal === 'biweek'"
                                class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left mb-3">
                                <select v-model="form.byMonthlys" class="form-control large-select" :v-model="form.quarterCycle">
                                    <option value="">{{ $t('Select') }}</option>
                                    <option v-for="day in daysOfWeek" :key="day" :value="day">{{ day }}</option>
                                </select>
                            </div>

                            <!-- Quarterly -->
                            <div v-if="BillFrequencyVal === 'quarterly'"
                                class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left mb-3">
                                <select v-model="form.quarterCycle" class="form-control large-select"
                                    :v-model="form.quarterCycle">
                                    <option value="">{{ $t('Select') }}</option>
                                    <option value="Q1 (JanMar)">Q1 (JanMar)</option>
                                    <option value="Q2 (AprJun)">Q2 (AprJun)</option>
                                    <option value="Q3 (JulSep)">Q3 (JulSep)</option>
                                    <option value="Q4 (OctDec)">Q4 (OctDec)</option>
                                    <option value="custom">Custom</option>
                                </select>

                                <div v-if="form.quarterCycle === 'custom'" class="mb-2 mt-3">
                                    <select v-model="form.customQuarterStartMonth" class="form-control large-select"
                                        :v-model="form.quarterlyBillDate">
                                        <option value="">{{ $t('Select') }}</option>
                                        <option v-for="month in months" :key="month" :value="month">{{ month }}
                                        </option>
                                    </select>
                                    <small class="text-muted">{{ $t('CycleRange') }}: {{ getCycleRange(3,
                                        form.customQuarterStartMonth) }}</small>
                                </div>
                            </div>
                            <div v-if="BillFrequencyVal === 'halfyear'"
                                class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left mb-3">
                                <select v-model="form.halfYearCycle" class="form-control large-select">
                                    <option value="">{{ $t('Select') }}</option>
                                    <option value="H1">JanJun</option>
                                    <option value="H2">JulDec</option>
                                    <option value="custom">Custom</option>
                                </select>

                                <div v-if="form.halfYearCycle === 'custom'" class="mb-2 mt-3">
                                    <select v-model="form.customQuarterStartMonth" class="form-control large-select">
                                        <option value="">{{ $t('Select') }}</option>
                                        <option v-for="month in months" :key="month" :value="month">{{ month }}
                                        </option>
                                    </select>
                                    <small class="text-muted">{{ $t('CycleRange') }}: {{ getCycleRange(6,
                                        form.customQuarterStartMonth) }}</small>
                                </div>
                            </div>
                            <!-- Annually -->
                             <div v-if="BillFrequencyVal === 'annual' && GenerationDate == true"
                                class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left mb-3">
                                <select v-model="form.annualMonth" class="form-control" :v-model="form.annualMonth">
                                    <option value="">{{ $t('Select') }}</option>
                                    <option v-for="month in months" :key="month" :value="month">{{ month }}</option>
                                </select>
                            </div>
                            <!-- Custom -->
                            <div v-if="BillFrequencyVal === 'custom'"
                                class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left mb-3">
                               <select v-model="form.customMonthsCount" class="form-control large-select">
                                    <option v-for="n in 12" :key="n" :value="n">{{ n }}</option>
                                </select>
                                <div  class="mb-2 mt-3">
                                   <select v-model="form.customQuarterStartMonth" class="form-control large-select me-3">
                                        <option v-for="month in months" :key="month" :value="month">{{ month }}</option>
                                    </select>
                                    <small class="text-muted">{{ $t('CycleRange') }}: {{ getCycleRange(form.customMonthsCount,
                                        form.customQuarterStartMonth) }}</small>
                                </div>
                            </div>
                        </div>
                    </template>
                            </dynamic-form>
                        </div>
                        
                    </div>
                </div>
            </div>
        </div>

    </section>
</template>
<script>
import DataService from "../../services/DataService";
export default {
    props: {
        callbackfunction: {
            type: Function,
            required: false
        },
        BatchId:{
            type: Number,
            required: false
        }
    },
    data() {
        return {
            FormModel:{
            isRecurring:"1"
            },
            selectedFrequencyTRype: '',
            isLoading: false,
            FormSchema: [
                {
                    layoutType: "triple",
                    Data: [
                        {
                            astype: "TextField",
                            label: this.$t('batchname'),
                            name: "BatchName",
                            value: "",
                            placeholder: 'Batch Name',
                            validationRules: { "required": true, max: 100 },
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('CustomerType'),
                            name: "CustomerType",
                            value: "",
                            config: {
                                options: []
                            },
                            placeholder: this.$t('SelectCustomerType'),
                            validationRules: { "required": false, max: 100 },
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Plan'),
                            name: "Plan",
                            value: "",
                            config: {
                                options: []
                            },
                            placeholder: this.$t('SelectPlan'),
                            validationRules: { "required": false, max: 100 },
                        }

                    ]
                },
                {
                    layoutType: "triple",
                    Data: [
                        {
                            astype: "SelectField",
                            label: this.$t('StationName'),
                            name: "Station",
                            value: "",
                            config: {
                                options: []
                            },
                            placeholder: this.$t('SelectStation'),
                            validationRules: { "required": false, max: 100 },
                        },
                        {
                            astype: "TextField",
                            label: this.$t('STATE'),
                            name: "State",
                            value: "",
                            placeholder: 'Enter State',
                            validationRules: { "required": false, max: 100 },
                        },
                        {
                            astype: "TextField",
                            label: this.$t('DistrictName'),
                            name: "District",
                            value: "",
                            placeholder: 'Enter District',
                            validationRules: { "required": false, max: 100 },
                        }
                     
                    ]
                },
                {
                    layoutType: "triple",
                    Data: [
                        {
                            astype: "TextField",
                            label: this.$t('ZIPCODE'),
                            name: "ZipCode",
                            value: "",
                            placeholder: 'Enter Zip Code',
                            validationRules: { "required": false, max: 100 },
                        },
                         {
                            astype: "SelectField",
                            label: this.$t('Number Of Cycle'),
                            name: "NoOfCycle",
                            value: "",
                            config: {
                                options: [{ value: "1", name: "1" }, { value: "2", name: "2" }, { value: "3", name: "3" }, { value: "4", name: "4" }, { value: "5", name: "5" }, { value: "6", name: "6" }, { value: "7", name: "7" }, { value: "8", name: "8" }, { value: "9", name: "9" }, { value: "10", name: "10" }]
                            },
                            placeholder: this.$t('Select Number Of Cycle'),
                            validationRules: { "required": false, max: 100 },
                            },
                    ],

                },
                
                {
                    layoutType: "triple",
                    Data: [{
                        astype: "toggleField",
                        label: this.$t('AdvanceSettings'),
                        name: "AdvanceSettings",
                        value: "",
                        placeholder: "",
                        visibility: false,
                        config: {
                            onChange: this.showAdvanceSettings,
                        },
                    },]

                },
                {
                    layoutType: "double",
                    group_name: "Reading:",
                    Data: [
                        {
                                astype: "SelectField",
                                label: this.$t('BillingFrequency'),
                                name: "BillingFrequency",
                                value: "",
                                placeholder: "",
                                visibility: (this.callfrom == "SUBSCRIPTION_BILLING" ? false : true),
                                validationRules: { "required": true },
                                config: {
                                    options: [{
                                        value: "week",
                                        name: 'Weekly'
                                    }, {
                                        value: "biweek",
                                        name: 'Bi-Weekly'
                                    }, {
                                        value: "month",
                                        name: "Monthly"
                                    },
                                    {
                                        value: "bymonthly",
                                        name: "Bi-Monthly"
                                    }, {
                                        value: "quarterly",
                                        name: "Quarterly"
                                    }, {
                                        value: "halfyear",
                                        name: "Half-Yearly"
                                    }, {
                                        value: "annual",
                                        name: "Annualy"
                                    }, {
                                        value: "custom",
                                        name: "Custom Date"
                                    }, 
                                    ],
                                    onChange: this.OnChangeBillFrequency,
                                },
                            },
                             {
                                astype: "SlotField",
                                label: "",
                                name: "BillingFrequencyType",
                                value: "",
                                visibility: true,
                                placeholder: "",
                                config: {
                                    options: [],
                                },
                                validationRules: "",
                            },
                             {
                            astype: "DateField",
                            label: this.$t('StartDate'),
                            name: "StartDate",
                            value: '',
                            placeholder: "",
                            config: {
                                format: "MM/DD/YYYY",
                                onChange: this.setEndMinDate,
                                maxDate: false
                            },
                            validationRules: { "required": true },
                        },
                        {
                            astype: "DateField",
                            label: this.$t('EndDate'),
                            name: "EndDate",
                            value: '',
                            placeholder: "",
                            config: {
                                format: "MM/DD/YYYY",
                                // onChange: this.setEndMinDate,
                                maxDate: false,
                                minDate: ''
                            },
                            validationRules: { "required": true },
                        },
                            {
                            astype: "SelectField",
                            label: this.$t('ReadingStartDate'),
                            name: "MeterReadingStartDate",
                            value: "",
                            placeholder: "",
                            visibility: true,
                            config: {
                                options: Array.from({ length: 31 }, (v, i) => ({
                                    value: i + 1,
                                    name: (i + 1).toString()
                                }))
                            },
                        },

                          {
                            astype: "SelectField",
                            label: this.$t('ReadingEndDate'),
                            name: "MeterReadingEndDate",
                            value: "",
                            placeholder: "",
                            visibility: true,
                            config: {
                                options: Array.from({ length: 31 }, (v, i) => ({
                                    value: i + 1,
                                    name: (i + 1).toString()
                                }))
                            },
                        },

                    ]

                },

                {
                    layoutType: "double",
                    group_name: "Billing:",
                    Data: [
                        {
                            astype: "SelectField",
                            label: this.$t('BillingStartDateDay'),
                            name: "MeterReadingStartDate1",
                            value: "",
                            placeholder: "",
                            visibility: true,
                            config: {
                                options: Array.from({ length: 31 }, (v, i) => ({
                                    value: i + 1,
                                    name: (i + 1).toString()
                                }))
                            },
                        },

                          {
                            astype: "SelectField",
                            label: this.$t('BillingEndDateDay'),
                            name: "MeterReadingEndDate2",
                            value: "",
                            placeholder: "",
                            visibility: true,
                            config: {
                                options: Array.from({ length: 31 }, (v, i) => ({
                                    value: i + 1,
                                    name: (i + 1).toString()
                                }))
                            },
                        },
                        {
                            astype: "DateField",
                            label: this.$t('BillingStartDateDay'),
                            name: "Start_Date1",
                            value: new Date(),
                            placeholder: "",
                            config: {
                                format: "MM/DD/YYYY",
                                onChange: this.setScEndMinDate,
                                maxDate: '',
                                minDate: new Date()
                            },
                            validationRules: { "required": false },
                            visibility: true
                        },
                        {
                            astype: "DateField",
                            label: this.$t('BillingEndDateDay'),
                            name: "End_Date2",
                            value: '',
                            placeholder: "",
                            config: {
                                format: "MM/DD/YYYY",
                                maxDate: '',
                                onChange: this.setEndTime,
                                minDate: ''
                            },
                            validationRules: { "required": false },
                            visibility: true,
                        },
                        {
                            astype: "DateField",
                            label: this.$t('EventStartTime'),
                            name: "Start_Time",
                            value: "",
                            placeholder: this.$t('EventStartTime'),
                            validationRules: { "required": true, max: 100 },
                            config: {
                                mode: "time",
                                format: "hh:mm A",
                                minDate: '',
                                onChange: this.setEndTimeAds
                            },
                            visibility: true,
                        },
                        {
                            astype: "DateField",
                            label: this.$t('EndTime'),
                            name: "End_Time",
                            value: "",
                            placeholder: this.$t('EndTime'),
                            validationRules: { "required": true, max: 100 },
                            config: {
                                mode: "time",
                                format: "hh:mm A",
                                minDate: ''
                            },
                            visibility: true,
                        },
                         {
                            astype: "SlotField",
                            label: this.$t('Occurs_Every'),
                            name: "Occurs_Every",
                            value: "",
                            placeholder: '',
                            validationRules: { "required": false, max: 100 },
                            visibility: true,
                        }

                    ]

                },
                // {
                //     layoutType: "double",
                //     group_name: "Frequency:",
                //     Data: [
                //         {
                //             astype: "SlotField",
                //             label: this.$t('Occurs_Every'),
                //             name: "Occurs_Every",
                //             value: "",
                //             placeholder: '',
                //             validationRules: { "required": false, max: 100 },
                //             visibility: true,
                //         }
                //     ]
                // }
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success save",
                    value: 'Y',
                    isVisible:true,
                    text: "<i class='fa fa-save pr-2'></i>" + this.$t('createbatch'),
                    onClick: this.GenerateBatch
                },
                 {
                    type: "submit",
                    class: "btn btn-success save",
                    value: 'Y',
                    isVisible:false,
                    text: "<i class='fa fa-save pr-2'></i>" + this.$t('UpdateBatch'),
                    onClick: this.GenerateBatch
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>" + this.$t('Cancel'),
                    onClick: this.Close
                }
            ],
             GenerationDate: true,
            form: {
                billingFrequencyType:"",
                    BillingFrequency: "",
                    monthlyBillDate: "",
                    weeklyDay: "",
                    biweeklyDay: "",
                    quarterCycle: "",
                    quarterlyBillDate: "",
                    halfYearlyBillDate: "",
                    customQuarterStartMonth: "",
                    byMonthly: "",
                    customMonthsCount:1,
                    annualMonth: ""
                },
                  frequencyOptions: [
                    { value: "week", name: "Weekly" },
                    { value: "biweek", name: "Bi-Weekly" },
                    { value: "month", name: "Monthly" },
                    { value: "quarterly", name: "Quarterly" },
                    { value: "halfyear", name: "Half yearly" },
                    { value: "annual", name: "Annually" },
                    { value: "custom", name: "Custom Date" },
                    { value: "daily", name: "Daily" },
                ],
                daysOfWeek: ["Monday", "Tuesday", "Wednesday", "Thursday", "Friday", "Saturday", "Sunday"],
                months: ["January", "February", "March", "April", "May", "June", "July", "August", "September", "October", "November", "December"]
        }
    },

mounted() {
    this.OnChangeIsRecurring({
        target: {
            getAttribute: () => "1"
        }
    }, {
        value: "1"
    });

    //const isAdvanceEnabled = this.FormModel?.AdvanceSettings === true;
      const isAdvanceEnabled = true;
    this.showAdvanceSettings(isAdvanceEnabled);
},

    created: async function () {
        let vm = this;
        vm.isLoading = true;
        await vm.GetDDLCustomerTypeData();
        await vm.GetDDLPlanData();
        await vm.GetDDLStationData();
        await vm.setScEndMinDate(new Date(), '');
        if(vm.BatchId>0){
            vm.buttons[0].isVisible=false;
            vm.buttons[1].isVisible=true;
            await vm.GetBatchProcessData();
        }else{
            vm.buttons[1].isVisible=false;
            vm.buttons[0].isVisible=true;
        }
        vm.isLoading = false;
    },
    methods: {
  showAdvanceSettings(isEnabled, event) {
    const vm = this;
    const selectedValue = "1";
    const fieldNames = [
        "MeterReadingStartDate1",  
        "MeterReadingEndDate2",    
        "Start_Date1",             
        "End_Date2",              
        "Start_Time",
        "End_Time",
        "Occurs_Every"
    ];
    vm.FormSchema.forEach((section) => {
        if (section.Data && Array.isArray(section.Data)) {
            section.Data.forEach((field) => {
                if (fieldNames.includes(field.name)) {

                    if (
                        (field.name === "MeterReadingStartDate1" || field.name === "MeterReadingEndDate2") &&
                        selectedValue === "1"
                    ) {
                        field.visibility = isEnabled;
                        field.validationRules = field.validationRules || {};
                        field.validationRules.required = isEnabled;
                        if (!isEnabled) field.value = "";
                    } 
                    else if (
                        (field.name === "Start_Date1" || field.name === "End_Date2") &&
                        selectedValue === "2"
                    ) {
                        field.visibility = isEnabled;
                        field.validationRules = field.validationRules || {};
                        field.validationRules.required = isEnabled;
                        if (!isEnabled) field.value = "";
                    } 
                    else if (
                        field.name === "Start_Time" || 
                        field.name === "End_Time" || 
                        field.name === "Occurs_Every"
                    ) {
                        field.visibility = isEnabled;
                        field.validationRules = field.validationRules || {};
                        field.validationRules.required = isEnabled;
                        if (!isEnabled) field.value = "";
                    } 
                    else {
                        field.visibility = false;
                        if (field.validationRules) field.validationRules.required = false;
                        field.value = "";
                    }
                }
            });
        }
    });

    vm.$refs.validateBatch.UpdateKeyValue();
},
        setEndMinDate(data, event) {
            this.FormSchema[2].Data[1].config.minDate = data;
            this.FormSchema[2].Data[2].config.minDate = data;
            if (this.FormSchema[2].Data[0].value >= this.FormSchema[2].Data[1].value) {
                this.FormSchema[2].Data[1].value = '';
            } else {
                //
            }
        },
           OnChangeBillFrequency: function (event, field) {
            var vm = this;
            vm.selectedFrequencyTRype = field.value;
            vm.BillFrequencyVal = field.value;
            vm.form.weeklyDay = "";
            vm.form.byMonthly = "";
            vm.form.byMonthlys = "";
            vm.form.quarterCycle = "";
            vm.form.halfYearCycle = "";
            vm.form.customQuarterStartMonth = "";
            vm.form.customMonthsCount="";
            vm.form.annualMonth="";
            switch (field.value) {
                case 'bymonthly':
                vm.$refs.validateBatch.updateProperty("BillingFrequencyType", "label", this.$t('BillingMonth'));
                break;

                case 'week':
                vm.$refs.validateBatch.updateProperty("BillingFrequencyType", "label", this.$t('billingdayofweek'));
                break;

                case 'biweek':
                vm.$refs.validateBatch.updateProperty("BillingFrequencyType", "label", this.$t('billingdayofweek'));
                break;

                case 'quarterly':
                vm.$refs.validateBatch.updateProperty("BillingFrequencyType", "label", this.$t('quartercycle'));
                break;

                case 'halfyear':
                vm.$refs.validateBatch.updateProperty("BillingFrequencyType", "label", this.$t('cycle'));
                break;

                case 'custom':
                vm.$refs.validateBatch.updateProperty("BillingFrequencyType", "label", this.$t('BillingMonthCount'));
                break;
                case 'annual':
                vm.$refs.validateBatch.updateProperty("BillingFrequencyType", "label", this.$t('BillingMonth'));
                break;
                case 'month':
                default:
                vm.$refs.validateBatch.updateProperty("BillingFrequencyType", "label", "");
                break;
                }
            vm.$refs.validateBatch.UpdateKeyValue();
            },

        setScEndMinDate(data, event) {
            const baseDate = new Date(data);
            const minDate = new Date(baseDate);
            minDate.setDate(minDate.getDate() + 5);
            this.FormSchema[4].Data[0].config.minDate = new Date();
            this.FormSchema[4].Data[1].config.minDate = baseDate;
            this.FormSchema[4].Data[1].value = minDate;
            this.FormSchema[4].Data[1].config.maxDate = minDate;
            this.FormSchema[4].Data[3].value = minDate;
            this.FormSchema[4].Data[2].value = baseDate;
        },

        setEndTime(data, event) {
            this.FormSchema[4].Data[3].value = data;
        },
        GetDDLCustomerTypeData: function () {
            var vm = this;
            var params = `id=&moduleName=&type=CUSTOMER_TYPE&search=`
            DataService.GetDDLData(params).then((response) => {
                if (response.data) {
                    var json = response.data;
                    if (json != null) {
                        vm.$refs.validateBatch.updateProperty("CustomerType", "config.options", response.data.DATA);
                        vm.$refs.validateBatch.UpdateKeyValue();
                    }
                }
            })
        },
        GetDDLPlanData: function () {
            var vm = this;
            var params = `id=&moduleName=&type=BILLING_CYCLE_BATCH_PLANS&search=`
            DataService.GetDDLData(params).then((response) => {
                if (response.data) {
                    var json = response.data;
                    if (json != null) {
                        vm.$refs.validateBatch.updateProperty("Plan", "config.options", response.data.DATA);
                        vm.$refs.validateBatch.UpdateKeyValue();
                    }
                }
            })
        },
        GetDDLStationData: function () {
            var vm = this;
            var params = `id=&moduleName=&type=STATION_LIST&search=`
            DataService.GetDDLData(params).then((response) => {
                if (response.data) {
                    var json = response.data;
                    if (json != null) {
                        vm.$refs.validateBatch.updateProperty("Station", "config.options", response.data.DATA);
                        vm.$refs.validateBatch.UpdateKeyValue();
                    }
                }
            })
        },
        Close: function () {
            var vueObj = this;
            vueObj.$parent.isCreateBatch = false;
        },
         getReadableDateTimeString:async function(timeStr, dateStr) {
            if (!timeStr || typeof timeStr !== 'string') return null;

            const today = new Date();
            const datePart = dateStr || today.toISOString().split('T')[0]; // "YYYY-MM-DD"
            const fullDateTimeStr = `${datePart}T${timeStr}`;

            const dateObj = new Date(fullDateTimeStr);

            return isNaN(dateObj.getTime()) ? null : dateObj.toString(); // full date-time string
        },

        extractTimeFromDateTime: function(dateTimeStr) {
            if (!dateTimeStr || typeof dateTimeStr !== 'string') return '';

            // If it's already just a time string (e.g., "14:30:00"), return as is
            if (dateTimeStr.match(/^\d{1,2}:\d{2}:\d{2}$/) || dateTimeStr.match(/^\d{1,2}:\d{2}\s(AM|PM)$/i)) {
                return dateTimeStr;
            }

            // Try to extract time from datetime string
            const timeMatch = dateTimeStr.match(/(\d{1,2}:\d{2}:\d{2}|\d{1,2}:\d{2}\s?(AM|PM)?)/i);
            return timeMatch ? timeMatch[1] : dateTimeStr;
        },

         GetBatchProcessData: async function () {
            var vm = this;
            var url =`batchId=${vm.BatchId}`;
            await DataService.GetBatchProcessDataById(url).then(async function (response) {
                if (response.data != "" && response.data != null) {
                   var data= response.data;
                   var AdvanceSetting=false;
                    vm.$refs.validateBatch.updateProperty("BatchName", "value", data.batch_name);
                   vm.$refs.validateBatch.updateProperty("CustomerType", "value", data.customer_type_id);
                   vm.$refs.validateBatch.updateProperty("Station", "value", data.station_id==null?"":data.station_id);
                   vm.$refs.validateBatch.updateProperty("Plan", "value", data.plan_id==null?"":data.plan_id);
                   vm.$refs.validateBatch.updateProperty("State", "value", data.state);
                   vm.$refs.validateBatch.updateProperty("District", "value", data.district);
                   vm.$refs.validateBatch.updateProperty("ZipCode", "value", data.zip_code);
                   vm.$refs.validateBatch.updateProperty("Billing_Date", "value", data.billing_date);
                    vm.$refs.validateBatch.updateProperty("NoOfCycle", "value", data.no_of_cycle);
                   if(data.sc_start_time!= "00:00:00"){
                        vm.$refs.validateBatch.updateProperty("AdvanceSettings", "value", true);
                        AdvanceSetting=true;
                   }else{
                        vm.$refs.validateBatch.updateProperty("AdvanceSettings", "value", false);
                        AdvanceSetting=false;
                   }
                   vm.$refs.validateBatch.updateProperty("IsRecurring", "value", "1");
                   await vm.OnChangeIsRecurring("","",true);
                    if (true==true) {  
                    vm.$refs.validateBatch.updateProperty("MeterReadingStartDate", "value", data.reading_cycle_start_day);
                    vm.$refs.validateBatch.updateProperty("MeterReadingEndDate", "value", data.reading_cycle_end_day);
                    if (data.sc_start_time!= "00:00:00") {
                        vm.$refs.validateBatch.updateProperty("MeterReadingStartDate1", "value",  data.sc_billing_start_day );
                        vm.$refs.validateBatch.updateProperty("MeterReadingEndDate2", "value",  data.sc_billing_end_day);
                        vm.$refs.validateBatch.updateProperty("Start_Time", "value", await vm.getReadableDateTimeString(data.sc_start_time));
                        vm.$refs.validateBatch.updateProperty("End_Time", "value", await vm.getReadableDateTimeString(data.sc_end_time));
                        vm.$refs.validateBatch.updateProperty("Occurs_Every", "value",  data.occures_every);
                    }
                    } else {  
                   vm.$refs.validateBatch.updateProperty("StartDate", "value", data.date_from);
                   vm.$refs.validateBatch.updateProperty("EndDate", "value", data.date_to);
                    if (data.sc_start_time!= "00:00:00") {
                        vm.$refs.validateBatch.updateProperty("Start_Date1", "value",  data.sc_start_date);
                        vm.$refs.validateBatch.updateProperty("End_Date2", "value",  data.sc_end_date);
                        vm.$refs.validateBatch.updateProperty("Start_Time", "value", await vm.getReadableDateTimeString(data.sc_start_time));
                        vm.$refs.validateBatch.updateProperty("End_Time", "value",  await vm.getReadableDateTimeString(data.sc_end_time));
                        vm.$refs.validateBatch.updateProperty("Occurs_Every", "value",  data.occures_every);
                    }
                    }  
                    vm.$refs.validateBatch.updateProperty("BillingFrequency", "value",  data.billing_frequency);
                    let field = {};
                    field.value = data.billing_frequency;
                    await vm.OnChangeBillFrequency("", field);
                    switch (data.billing_frequency) {
                    case "month":
                    case "annual":
                        vm.form.annualMonth= data.billing_frequency_value; 
                        break;
                    case "week":
                        vm.form.weeklyDay= data.billing_frequency_value; 
                        break;
                    case "biweek":
                        vm.form.byMonthlys= data.billing_frequency_value; 
                        break;
                    case "bymonthly":
                        vm.form.byMonthly= data.billing_frequency_value;
                        break;
                    case "quarterly":
                        if (data.billing_frequency_value === "custom") {
                            vm.form.customQuarterStartMonth= data.custom_start_month;
                            vm.form.quarterCycle=data.billing_frequency_value;
                        } else {
                            vm.form.quarterCycle=data.billing_frequency_value;
                        }
                        break;
                    case "halfyear":
                        if (data.billing_frequency_value === "custom") {
                            vm.form.customQuarterStartMonth= data.custom_start_month; 
                            vm.form.halfYearCycle=data.billing_frequency_value;
                        } else {
                            vm.form.halfYearCycle=data.billing_frequency_value;
                        }
                        break;
                    case "custom": 
                        vm.form.customQuarterStartMonth=  data.custom_start_month;
                        vm.form.customMonthsCount= data.custom_months_count;
                        break;
                    default:
                        break;
                    }
                    await vm.showAdvanceSettings(AdvanceSetting)
                    vm.$refs.validateBatch.UpdateKeyValue();
                }
            })

        },
        async GenerateBatch(data, event) { 
            var vm = this;
            const result = await vm.$refs.validateBatch.validateForm.validate();
            if (result) {
                let BatchName= vm.$refs.validateBatch.getPropertyValue("BatchName", "value");
                let customerType = vm.$refs.validateBatch.getPropertyValue("CustomerType", "value");
                let station = vm.$refs.validateBatch.getPropertyValue("Station", "value");
                let plan = vm.$refs.validateBatch.getPropertyValue("Plan", "value");
                let state = vm.$refs.validateBatch.getPropertyValue("State", "value");
                let district = vm.$refs.validateBatch.getPropertyValue("District", "value");
                let zipCode = vm.$refs.validateBatch.getPropertyValue("ZipCode", "value");
                let startDate = vm.$refs.validateBatch.getPropertyValue("StartDate", "value");
                let endDate = vm.$refs.validateBatch.getPropertyValue("EndDate", "value");
                let billingDate = vm.$refs.validateBatch.getPropertyValue("Billing_Date", "value");
                let advanceSettings = true;
                //vm.$refs.validateBatch.getPropertyValue("AdvanceSettings", "value");
                let NumberOfCycle= vm.$refs.validateBatch.getPropertyValue("NoOfCycle", "value");
                let isRecurringRaw = vm.$refs.validateBatch.getPropertyValue("IsRecurring", "value");
                if (Array.isArray(isRecurringRaw)) {
                    isRecurringRaw = isRecurringRaw[0];  
                }
                let isRecurring = 1; //(isRecurringRaw === "1") ? 1 : 0;

                let scStartDate = null;
                let scEndDate = null;
                let scStartTime = '';
                let scEndTime = '';
                let occursEvery = 0;
                
                let scBillingStartDay = null; 
                let scBillingEndDay = null;   

                let readingCycleStartDay = null;
                let readingCycleEndDay = null;

                if (isRecurring === 1) {  
                    readingCycleStartDay = vm.$refs.validateBatch.getPropertyValue("MeterReadingStartDate", "value");
                    readingCycleEndDay = vm.$refs.validateBatch.getPropertyValue("MeterReadingEndDate", "value");

                    if (advanceSettings) {
                        scBillingStartDay = vm.$refs.validateBatch.getPropertyValue("MeterReadingStartDate1", "value");
                        scBillingEndDay = vm.$refs.validateBatch.getPropertyValue("MeterReadingEndDate2", "value");

                        let startTimeValue = vm.$refs.validateBatch.getPropertyValue("Start_Time", "value");
                        let endTimeValue = vm.$refs.validateBatch.getPropertyValue("End_Time", "value");

                        // Extract only time part from the datetime string returned by time fields
                        scStartTime = startTimeValue ? startTimeValue.toString().match(/(\d{1,2}:\d{2}:\d{2})/) ? startTimeValue.toString().match(/(\d{1,2}:\d{2}:\d{2})/)[1] : startTimeValue : '';
                        scEndTime = endTimeValue ? endTimeValue.toString().match(/(\d{1,2}:\d{2}:\d{2})/) ? endTimeValue.toString().match(/(\d{1,2}:\d{2}:\d{2})/)[1] : endTimeValue : '';
                        occursEvery = vm.$refs.validateBatch.getPropertyValue("Occurs_Every", "value");
                    }
                } else {  
                    startDate = vm.$refs.validateBatch.getPropertyValue("StartDate", "value");
                    endDate = vm.$refs.validateBatch.getPropertyValue("EndDate", "value");

                    if (advanceSettings) {
                        scStartDate = vm.$refs.validateBatch.getPropertyValue("Start_Date1", "value");
                        scEndDate = vm.$refs.validateBatch.getPropertyValue("End_Date2", "value");

                        let startTimeValue = vm.$refs.validateBatch.getPropertyValue("Start_Time", "value");
                        let endTimeValue = vm.$refs.validateBatch.getPropertyValue("End_Time", "value");

                        // Extract only time part from the datetime string returned by time fields
                        scStartTime = startTimeValue ? startTimeValue.toString().match(/(\d{1,2}:\d{2}:\d{2})/) ? startTimeValue.toString().match(/(\d{1,2}:\d{2}:\d{2})/)[1] : startTimeValue : '';
                        scEndTime = endTimeValue ? endTimeValue.toString().match(/(\d{1,2}:\d{2}:\d{2})/) ? endTimeValue.toString().match(/(\d{1,2}:\d{2}:\d{2})/)[1] : endTimeValue : '';
                        occursEvery = vm.$refs.validateBatch.getPropertyValue("Occurs_Every", "value");
                    }
                }
                let billingFrequencyType = vm.$refs.validateBatch.getPropertyValue("BillingFrequency", "value");
                let frequencyValue = ''; 
                let customStartMonth = null;
                let customMonthsCount=null;

                switch (billingFrequencyType) {
                    case "month":
                    case "annual":
                        frequencyValue = vm.form.annualMonth; 
                        break;
                    case "week":
                        frequencyValue = vm.form.weeklyDay;
                        break;
                    case "biweek":
                        frequencyValue = vm.form.byMonthlys; 
                        break;
                    case "bymonthly":
                        frequencyValue = vm.form.byMonthly;
                        break;
                    case "quarterly":
                        if (vm.form.quarterCycle === "custom") {
                            frequencyValue = "custom";  
                            customStartMonth = vm.form.customQuarterStartMonth;
                        } else {
                            frequencyValue = vm.form.quarterCycle;
                        }
                        break;
                    case "halfyear":
                        if (vm.form.halfYearCycle === "custom") {
                            frequencyValue = "custom";  
                            customStartMonth = vm.form.customQuarterStartMonth; 
                        } else {
                            frequencyValue = vm.form.halfYearCycle;
                        }
                        break;
                    case "custom":
                        frequencyValue = "custom";  
                        customStartMonth = vm.form.customQuarterStartMonth;
                        customMonthsCount= vm.form.customMonthsCount;
                        break;
                    default:
                        frequencyValue = '';
                        break;
                }

                if (
                    customerType === '' && station === '' && plan === '' &&
                    state === '' && district === '' && zipCode === ''
                ) {
                    return vm.ShowAlert(vm.$t("PleaseSelectOneField"), "warning", vm.$t('Alert'));
                }

                var objData = {
                    id: vm.BatchId,
                    batch_name: BatchName,
                    customerType: customerType,
                    stationId: station,
                    planId: plan,
                    state: state,
                    district: district,
                    zipCode: zipCode,
                    startDate: startDate,
                    endDate: endDate,
                    billingDate: billingDate,
                    scStartDate: scStartDate,
                    scEndDate: scEndDate,
                    scStartTime: scStartTime,
                    scEndTime: scEndTime,
                    occursEvery: occursEvery,
                    scBillingStartDay: scBillingStartDay,  
                    scBillingEndDay: scBillingEndDay,      
                    frequencyValue: frequencyValue,         
                    billingFrequencyType: billingFrequencyType,
                    custom_start_month: customStartMonth,
                    readingCycleStartDay: readingCycleStartDay,
                    readingCycleEndDay: readingCycleEndDay,
                    isRecurring: isRecurring,  
                    custom_months_count: customMonthsCount,
                    no_of_cycle: NumberOfCycle
                };
                var obj = JSON.stringify(objData);
                await DataService.CreateBillingBatchProcess(obj).then(response => {
                    if (response.data != null) {
                        if (response.data.Status == "Success" && response.data.Code == "1") {
                            if(vm.BatchId>0){
                                vm.ShowAlert(vm.$t('UpdatedSuccess', [vm.$t('Batch')]), "Success", true, vm.$t('Alert'));
                            }else{
                                vm.ShowAlert(vm.$t('AddedSuccess', [vm.$t('Batch')]), "Success", true, vm.$t('Alert'));
                            }
                            vm.Close();
                            vm.callbackfunction();
                            vm.isLoading = false;
                        }
                        else if (response.data.Status == "Success" && response.data.Code == "2") {
                            vm.ShowAlert("No data was found", "warning");
                            vm.isLoading = false;
                        }
                        else {
                            vm.ShowAlert(vm.$t("UnknownError"), "failure", vm.$t('Alert'));
                            vm.isLoading = false;
                        }
                    }
                });
            }
        },

          getCycleRange(len, start) {
                if (!start || len <= 0) return "";
                const idx = this.months.indexOf(start);
                if (idx === -1) return "";
                const list = [];
                for (let i = 0; i < len; i++) {
                    list.push(this.months[(idx + i) % 12]);
                }
                return list.join("  ");
            },

            gethelfYearCycleRange() {
                const month = this.form.customQuarterStartMonth;
                const index = this.months.indexOf(month);
                const nextMonths = [
                    this.months[index % 12],
                    this.months[(index + 1) % 12],
                    this.months[(index + 2) % 12],
                ];
                return `${nextMonths.join("  ")}`;
            },        
            
              resetFrom() {
                return {
                    BillingFrequency: "",
                    monthlyBillDate: "",
                    weeklyDay: "",
                    biweeklyDay: "",
                    quarterCycle: "",
                    quarterlyBillDate: "",
                    halfYearlyBillDate: "",
                    customQuarterStartMonth: "",
                };
            },
             BillGenerateDropDown: function () {
                var vm = this;
                vm.$refs.refAddress.updateProperty("BillGenerationDate", "config.options", vm.dateArray);
                vm.$refs.refAddress.UpdateKeyValue();
            },

            OnChangeIsRecurring: function (event, field, EditValue) {
                var vm = this;
                let selectedValue="";
                if(EditValue ==undefined){
                //field.value = event?.target?.getAttribute('data-value') || field?.value || "1";
                field.value ="1";
                selectedValue = field.value;
                }else{
                     selectedValue = EditValue==true?"1":"2";
                }
                const replaceFields = [
                    "MeterReadingStartDate",
                    "MeterReadingEndDate",
                    "MeterReadingStartDate1",
                    "MeterReadingEndDate2",
                    "StartDate",
                    "EndDate",
                    "Start_Date1",
                    "End_Date2"
                ];

                replaceFields.forEach(name => {
                    vm.$refs.validateBatch.updateProperty(name, "visibility", false);
                    vm.$refs.validateBatch.updateProperty(name, "value", "");
                });
                if (selectedValue === "1") { 
                    vm.$refs.validateBatch.updateProperty("MeterReadingStartDate", "visibility", true);
                    vm.$refs.validateBatch.updateProperty("MeterReadingEndDate", "visibility", true);
                    if(true == true)
                    {
                        vm.$refs.validateBatch.updateProperty("MeterReadingStartDate1", "visibility", true);
                        vm.$refs.validateBatch.updateProperty("MeterReadingEndDate2", "visibility", true);
                    }
                    
                } else if (selectedValue === "2") { 
                    vm.$refs.validateBatch.updateProperty("StartDate", "visibility", true);
                    vm.$refs.validateBatch.updateProperty("EndDate", "visibility", true);
                    if(true == true)
                    {
                        vm.$refs.validateBatch.updateProperty("Start_Date1", "visibility", true);
                        vm.$refs.validateBatch.updateProperty("End_Date2", "visibility", true);
                    }
                }
                vm.$refs.validateBatch.UpdateKeyValue();
            }
    }

}
</script>
